.noticeWrapper {
  padding: 5px;

  position: absolute;
  transform: translateX(-50%);

  font-family: 'Comfortaa';
  font-size: 5px;
  color: #fff;
  font-weight: bold;
  text-align: center;
  word-wrap: none;
  white-space: nowrap;

  background-color: rgba(158, 156, 156, 0.7);
  border-radius: 10px;
  
  user-select: none;
}
